@media (max-width: 1200px) {

    .menu {

        .menu_item {
        padding-right: 20px;
        }
        
        .menu_link {
        font-size: 13px;
        }
        
        }

        .promo {

        .promo_header {
            font-size: 50px;
        }


        }

        .reasons {

        .reasons_block {
            align-items: flex-start;
        }

        .reasons_descr {
        width: 300px;
        }

        }

}

@media (max-width: 991px) {

    .menu {

        .menu_item {
        padding-right: 8px;
        }
        
        .menu_link {
        font-size: 10px;
        }
        
        }

        .subheader {
            .subheader_logo img {
                width: 100%;
            }

            .subheader_official {
                font-size: 11px;
            }

            .subheader_phone {
                font-size: 12px;
            }

            .subheader_btn {
                margin-top: 21px;
                height: 40px;
                width: 100%;
            }

        }

        .promo {

            .promo_header {
                font-size: 40px;
            }
    
            .promo_subheader {
                font-size: 30px;
            }

    
            }
    
            reasons {

                .reasons_block {
                    flex-direction: column;
                    align-items: center;
                }
        
                .reasons_descr {
                width: 300px;
                margin-top: 20px;
                }
        
                }


}

@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, .8);
        transition: 0.6s all;
    }

    .menu :active {
        left: 0;
    }

    .menu_item {
        padding-right: 0;
        border: none;
        margin: 10px 0;
    }
        
    .menu_link {
        font-size: 20px;
    }

    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
    }

    .hamburger span {
    display: block;
    height: 2px;
    width: 100%;
    background-color: #fff;
    margin-bottom: 2px;
    }

    .hamburger span :active {
        background-color: coral;
        transform: rotate(-45deg);
    }

}

