header {
    position: relative;
    height: 50px;
  }
  
  nav {
    position: fixed;
    width: 100%;
    height: 50px;
    background-color: #212121;
    z-index: 2;
  }

  .hamburger {
    display: none;
  }
  
  .menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
  
    .menu_link {
      color: #ffffff;
      font-size: 15px;
      font-weight: 300;
    }
  
    .menu_item {
      border-right: 2px dotted black;
      padding-right: 28px;
    }
  }
  
  .subheader {
    position: absolute;
    top: 50px;
  
    /* * */
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
  
    .subheader_logo {
      display: block;
      max-width: 100%;
      margin-top: 16px;
    }
  
    .subheader_official {
      margin-top: 6px;
      opacity: 0.5;
      color: #ffffff;
      font-size: 13px;
      font-weight: 400;
    }
  
    .subheader_call {
      color: #ffffff;
      font-size: 12px;
      font-weight: 300;
      margin-top: 19px;
      text-align: right;
    }
  
    .subheader_phone {
      display: block;
      margin-top: 8px;
      color: #ffffff;
      font-size: 22px;
      font-weight: 400;
      text-transform: uppercase;
      text-align: right;
      line-height: 12px;
      text-decoration: none;
    }
  
    .subheader_btn {
      margin-top: 19px;
      width: 172px;
      height: 46px;
      box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
      border: none;
      border-radius: 4px;
      background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
      text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
      color: #ffffff;
      font-size: 14px;
      font-weight: 400;
      text-transform: uppercase;
      @include btn_hover()
    }
  }


  
  
  .promo {
    min-height: 800px;
    background: url('../img/uber_bg.png') center (center / cover) no-repeat;
    padding: 150px 0 135px 0;
  
    .promo_header {
      color: #ffffff;
      font-weight: 700;
      text-transform: uppercase;
      font-size: 60px;
      line-height: 60px;
      margin: 0;
      text-align: center;
    }
  
    .promo_subheader {
      color: #ffffff;
      font-weight: 700;
      text-transform: uppercase;
      font-size: 36px;
      line-height: 36px;
      margin: 0;
      margin-top: 22px;
      text-align: center;
    }
  
    .promo_text {
      color: #ffffff;
      font-size: 18px;
      font-weight: 400;
      line-height: 24px;
      text-align: center;
      margin-top: 23px;
    }
  
    .promo_btn {
      display: block;
      margin: 118px auto 0 auto;
      width: 259px;
      height: 63px;
      box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
      border: none;
      border-radius: 4px;
      background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
      text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
      color: #ffffff;
      font-size: 18px;
      font-weight: 400;
      text-transform: uppercase;
      @include btn_hover()
    }
  }
  
  .reasons {
    padding: 71px 0 65px 0;
  }
  
  .label {
    margin: 0 auto;
    height: 24px;
    width: 112px;
