    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
  }
  
  .title {
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
