footer {
min-height: 133px;

.footer_divider {
width: 301px;
height: 7px;
background: #1FBAD6;
margin: 0 auto;
margin-top: 11px;
}

.footer_wrapper {
display: flex;
justify-content: space-between;
align-items: flex-start;
padding-top: 32px;
}


.footer_social {
width: 160px;
display: flex;
justify-content: space-between;
}

.footer_social_item {
  display: flex;
	justify-content: center;
	align-items: center;
	margin: 0 4px;
	width: 32px;
	height: 32px;
	background-color: #070716;
	border-radius: 100%;
}

.footer_links {
width: 390px;
margin: 0 auto;
}

.footer_links_main {
  display: flex;
	justify-content: space-between;
	padding: 0 10px;
}

.footer_links_main a {
  color: #070716;
	font-size: 15px;
	font-weight: 300;
	text-transform: uppercase;
}

.footer_links_sub {
  display: flex;
	justify-content: space-between;
	margin-top: 10px;
}

.footer_links_sub a {
  color: #999999;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
}

.footer_links_lang {
  display: block;
	margin: 0 auto;
	color: #1fbad6;
	font-size: 11px;
	font-weight: 400;
	margin-top: 19px;
	text-transform: uppercase;
	text-align: center;
}


}
